export default {
	data() {
		return {
			userInfo:{}
		}
	},
	onShow(){
		this.infoData()
	},
	methods: {
		infoData(){
			let self=this
			this.$acFrame.HttpService.ownerInfoMain().then(res=>{
				if(res.success){
					self.userInfo = res.data
				}
			})
		},
		linkTo(url){
			uni.navigateTo({
				url: url
			});
		}
	}
}
